.wrapper {
  max-width: 1197px;
  height: 98vh;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }
  @media (max-width: 1197px) {
    .wrapper {
      padding: 0 46px; } }
  @media (max-width: 414px) {
    .wrapper {
      padding: 0 21px; } }

.keyboard {
  margin-top: 1vh;
  margin-right: 1vw;
  padding: 5px 10px;
  width: 80rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  color: #f6faf6;
  font-weight: 400;
  letter-spacing: 0.8px;
  text-shadow: -1px -1px 1px black;
  background-color: #000000;
  border-radius: 5px;
  box-shadow: 5px 5px 5px 2px #000038;
  -moz-user-select: none;
  user-select: none; }
  @media screen and (max-width: 900px) {
    .keyboard {
      transform: scale(0.95); } }
  @media (max-width: 768px) {
    .keyboard {
      transform: scale(0.89); } }
  @media screen and (max-width: 900px) {
    .keyboard {
      transform: scale(0.75); } }
  @media (max-width: 414px) {
    .keyboard {
      transform: scale(0.42); } }
  @media (max-width: 320px) {
    .keyboard {
      transform: scale(0.4); } }

.text-field {
