.key {
  margin: 4px;
  padding: 3px 8px 8px 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-basis: 5.5%;
  flex-grow: 1;
  height: 4rem;
  font-size: 1.6rem;
  background-color: #3f4c61;
  border-radius: 5px;
  box-shadow: inset -2px -2px 3px 2px #000038;
  cursor: pointer;
  transition: all 0.1s linear; }
  .key:first-child {
    flex-basis: 3.5%; }
  .key.special_key {
    background-color: #212636;
    font-size: 1rem; }
    .key.special_key-one_quarter {
      flex-basis: 7%; }
      .key.special_key-one_quarter-slim {
        height: 1.25rem; }
    .key.special_key-one_half {
      flex-basis: 9%; }
    .key.special_key-double_quarter {
      flex-basis: 13%; }
    .key.special_key-double_half {
      flex-basis: 15%; }
      .key.special_key-double_half.active {
        transform: none; }
    .key.special_key-seven {
      flex-basis: 35%; }
    .key.special_key.active {
      transform: scale(0.995, 0.95);
      font-size: 1rem; }
  .key.active {
    padding: 4px 8px 8px 4px;
    box-shadow: inset -2px -2px 2px #000038;
    background-color: #525661;
    font-size: 1.7rem;
    transform: scale(0.95);
    transform-origin: 100% 100%; }

.up-down-arrows {
  display: flex;
  flex-direction: column;
  flex-basis: 8%; }
  .up-down-arrows .key {
    margin-top: 1px;
    margin-bottom: 1px; }
    .up-down-arrows .key.active {
      padding: 3px 8px 8px 3px; }
